@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500;600;700&display=swap);
body, html, #root {
  margin: 0 !important;
  padding: 0 !important;
  width: 100%;
  position: absolute;
  display: block;
  font-size: 18px;
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background-color: rgba(89, 19, 52, 1);
}
#root{
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.color2{
  color: #C9472D !important;
}
.colorb2{
  background-color: #C9472D !important;
}

.btn{
  height: 40px;
  width: auto;
  line-height: 40px;
  display: inline-block;
  padding: 0 20px;
  cursor: pointer;
  border-radius: 50px;
  color: #ffffff;
  text-decoration: none;
}

.btn.border-t-point{
  border-radius: 10px 0 10px !important;
}

.btn.border{
    border: 1px solid #FFFF;
    border-radius: 10px;
}

.btn.border.color2{
  color: #C9472D !important;
  border: 1px solid #C9472D !important;
}

.flex{
  display: -webkit-flex;
  display: flex;
}
/* flex-direction */
.flex-dc{ -webkit-flex-direction: column; flex-direction: column; }
.flex-dr{ -webkit-flex-direction: row; flex-direction: row; }
.flex-dcr{ -webkit-flex-direction: column-reverse; flex-direction: column-reverse; }
.flex-drr{ -webkit-flex-direction: row-reverse; flex-direction: row-reverse; }

/* align-items */
.flex-ic{ -webkit-align-items: center; align-items: center; }
.flex-is{ -webkit-align-items: flex-start; align-items: flex-start; }
.flex-ie{ -webkit-align-items: flex-end; align-items: flex-end; }
.flex-ist{ -webkit-align-items: stretch; align-items: stretch; }
.flex-ii{ -webkit-align-items: inherit; align-items: inherit; }

.flex-jc{ -webkit-justify-content: center; justify-content: center; }
.flex-js{ -webkit-justify-content: flex-start; justify-content: flex-start; }
.flex-je{ -webkit-justify-content: flex-end; justify-content: flex-end; }
.flex-jst{ -webkit-justify-content: stretch; justify-content: stretch; }
.flex-jsb{ -webkit-justify-content: space-between; justify-content: space-between; }
.flex-jsa{ -webkit-justify-content: space-around; justify-content: space-around; }
.flex-jse{ -webkit-justify-content: space-evenly; justify-content: space-evenly; }
.flex-ji{ -webkit-justify-content: inherit; justify-content: inherit; }

@media screen and (max-width: 750px) {
  .container{
    height: 100%;
    width: 100%;
  }

}

@media screen and (max-height: 500px) {
  body, html{
    font-size: 18px;
  }
  .container{
    height: 100%;
    width: 100%;
  }
}

